export default "<template>\r\n  <nm-split v-model=\"split\">\r\n    <template v-slot:left>\r\n      <nm-listbox title=\"已选用户\" v-model=\"listbox\"></nm-listbox>\r\n    </template>\r\n    <template v-slot:right>\r\n      <nm-list v-bind=\"list\">\r\n        <template v-slot:querybar>\r\n          <el-form-item label=\"姓名：\" prop=\"name\">\r\n            <el-input v-model=\"list.model.name\"/>\r\n          </el-form-item>\r\n        </template>\r\n\r\n        <template v-slot:col-operation=\"{row}\">\r\n          <nm-button type=\"text\" text=\"选择\" @click=\"onSelect(row)\"/>\r\n        </template>\r\n      </nm-list>\r\n    </template>\r\n  </nm-split>\r\n</template>\r\n<script>\r\nexport default {\r\n  data () {\r\n    return {\r\n      split: 0.3,\r\n      listbox: [],\r\n      list: {\r\n        title: '用户列表',\r\n        icon: 'list',\r\n        action: this.query,\r\n        model: {\r\n          name: ''\r\n        },\r\n        cols: [{\r\n          name: 'id',\r\n          label: '编号',\r\n          show: false\r\n        },\r\n        {\r\n          name: 'name',\r\n          label: '姓名'\r\n        },\r\n        {\r\n          name: 'age',\r\n          label: '年龄'\r\n        }\r\n        ]\r\n      }\r\n    }\r\n  },\r\n  methods: {\r\n    query () {\r\n      const rows = [{ id: 1, name: '张三', age: 22 }, { id: 2, name: '李四', age: 26 }, { id: 3, name: '王五', age: 26 }]\r\n      return new Promise(resolve => {\r\n        resolve({\r\n          rows,\r\n          total: 3\r\n        })\r\n      })\r\n    },\r\n    onSelect (row) {\r\n      this.listbox.push({\r\n        label: row.name,\r\n        value: row.id\r\n      })\r\n    }\r\n  }\r\n}\r\n</script>\r\n"